.services__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
}

.service__head h3 {
  color: var(--color-bg-variant);
  font-size: 1rem;
  text-align: center;
}

.service {
  background: var(--color-bg-variant);
  color: var(--color-bg);
  border-radius: 0 0 2rem 2rem;
  border: 1px solid #2c3639;
  height: fit-content;
  transition: var(--transition);
}

.service:hover {
  background: transparent;
  cursor: default;
  color: var(--color-bg-variant);
}

.service__head {
  background: #2c3639;
  padding: 2rem;
  border-radius: 0 0 2rem 2rem;
  box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.service__list {
  padding: 2rem;
}

.service__list li {
  display: flex;
  gap: 1rem;
  margin-bottom: 0.8rem;
}

.service__list-icon-1 {
  font-size: 25px;
}

.service__list p {
  font-size: 0.9rem;
}

@media screen and (max-width: 1024px) {
    .services__container{
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }
    .servicep{
        height: auto;
    }
}

@media screen and (max-width: 600px) {
    .services__container{
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
}
